nav {
    width: 100vw;
    display: flex;
    justify-content: space-between;
    background-color: #001219;
    align-items: center;
}

#heading{
    color: #2a2727;
}

span, #MoviesLight{
    color: #151313;
}
nav h1{
    color: #ff206e;
    margin-left: 40px;
}
nav a {
    text-decoration: none;
}
nav span{
    color: #eac786;
    cursor: pointer;
    margin-left: 40px;
    font-size: .99rem;
    text-transform: uppercase;
}

nav span:hover{
    color: #EE9B00;
    border-bottom: 2px solid #EE9B00;
}

.navBarColor{
    background-color: #b4d3b6;
    transition: .6s;
}
.nav-options{
    display: flex;
    align-items: center;
}
.input-group{
    display: flex;
    align-items: center;
}
#Color-switcher{
    position: relative;
    margin-right: 40px;
    background-color: #413E44;
    width: 80px;
    height: 40px;
    border-radius: 20px;
}
#Color-switcher-mover{
    position: absolute;
    left: 5px;
    top: 4px;
    background-color: #fff;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    transition: .6s;
}
#Color-switcher-moved{
    position: absolute;
    background-color: #fff;
    left: 40px;
    top: 4px;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    transition: .6s;
}

.input-group input{
    position: relative;
    width: 323px;
    height: 4vh;
    border-radius: 16px;
    border: none;
    margin-right: 30px;
}
.input-group input[type='text']{
    padding-left: 17px;
    font-size: 1rem;
    color: black;
}
.input-group input::placeholder{
    color: black;
}
.input-group input:focus{
    outline: none;
}
#search{
    position: absolute;
    margin-left: 310px;
}
